// Proper ZIP file creation using JSZip library
const createZipFile = async (files: { name: string; content: string }[]): Promise<Blob> => {
    const zip = new JSZip();

    // Add each file to the zip
    files.forEach(file => {
        zip.file(file.name, file.content);
    });

    // Generate the zip file as a blob
    return await zip.generateAsync({ type: 'blob' });
};

// Function to fetch entire scene folder contents
const fetchSceneContents = async (sceneName: string, dirPath: string, folderInZip: JSZip): Promise<void> => {
    let potentialFiles = [];

    // Determine scene type based on the directory path
    const isManipulationTask = dirPath.includes('manipulation%20tasks') || dirPath.includes('manipulation tasks');

    if (isManipulationTask) {
        // Manipulation task specific files
        potentialFiles = [
            // Common scene files
            'README.md', 'scene.xml', 'container.xml',

            // Assets folder patterns (for manipulation tasks)
            ...Array.from({length: 20}, (_, i) => `assets/model/model_${i}.obj`),
            'assets/robotiq_2f85/base_mount.stl', 'assets/robotiq_2f85/coupler.stl', 'assets/robotiq_2f85/driver.stl',
            'assets/robotiq_2f85/pad.stl', 'assets/robotiq_2f85/follower.stl', 'assets/robotiq_2f85/base.stl',
            'assets/robotiq_2f85/spring_link.stl', 'assets/robotiq_2f85/silicone_pad.stl'
        ];

        // Add scene-specific assets based on scene name
        if (sceneName.includes('mug_tree')) {
            potentialFiles.push('assets/mug_tree/model.obj', 'assets/mug_tree/texture.png',
                              'assets/vuer-mug/mug.obj', 'assets/vuer-mug/vuer.png');
        }
    } else {
        // Physics playground specific files
        potentialFiles = [
            // Common scene files
            'README.md', 'scene.xml', 'container.xml', 'mannequin.xml', 'humanoid.png', 'humanoid.xml',

            // Asset files - try common patterns
            'asset/README.md', 'asset/bunny.obj', 'asset/bunny_with_uv.obj', 'asset/cap.obj', 'asset/sponge.png', 'asset/spot.obj', 'asset/spot.png',

            // Physics playground specific files
            'bunny.obj', 'assets/card.obj',
            'bowl/bowl.mjcf.xml', 'bowl/scene.xml', 'bunnies/bunnies.mjcf.xml', 'bunnies/bunny.obj', 'bunnies/container.xml',
            'cow/cow.mjcf.xml', 'cow/scene.xml', 'cow/asset/spot.obj', 'cow/asset/spot.png', 'cylinder/cylinder.mjcf.xml',
            'flag/flag.mjcf.xml', 'floppy/floppy.mjcf.xml', 'floppy/scene.xml', 'gear/gear.mjcf.xml', 'gear/scene.xml',
            'hammock/hammock.mjcf.xml', 'hammock/humanoid.png', 'hammock/humanoid.xml', 'helix/helix.mjcf.xml',
            'jelly/jelly.mjcf.xml', 'jelly/scene.xml', 'leaves/leaves.mjcf.xml', 'mug/mug.mjcf.xml', 'mug/scene.xml',
            'newton_cradle/newton_cradle.mjcf.xml', 'nutbolt/nutbolt.mjcf.xml', 'nutbolt/scene.xml',
            'particle/particle.mjcf.xml', 'particle/scene.xml', 'poncho/mannequin.xml', 'poncho/poncho.mjcf.xml',
            'pulley/pulley.mjcf.xml', 'pulley/scene.xml', 'stonehenge/stonehenge.mjcf.xml', 'torus/scene.xml', 'torus/torus.mjcf.xml'
        ];

        // Add card assets for physics playground
        const suits = ['clubs', 'diamonds', 'hearts', 'spades'];
        const values = ['2', '3', '4', '5', '6', '7', '8', '9', '10', 'jack', 'queen', 'king', 'ace'];
        for (const value of values) {
            for (const suit of suits) {
                potentialFiles.push(`assets/${value}_of_${suit}.png`);
            }
        }
        potentialFiles.push('assets/black_joker.png', 'assets/red_joker.png');
    }

    // Process all potential files
    let filesAdded = 0;
    for (const fileName of potentialFiles) {
        try {
            const fileUrl = `${dirPath}/${fileName}`;
            const response = await fetch(fileUrl, {
                mode: 'cors',
                headers: {
                    'Accept': '*/*'
                }
            });
            if (response.ok) {
                if (fileName.endsWith('.png') || fileName.endsWith('.jpg') || fileName.endsWith('.obj') || fileName.endsWith('.mtl') || fileName.endsWith('.stl')) {
                    const blob = await response.blob();
                    folderInZip.file(fileName, blob);
                } else {
                    const content = await response.text();
                    folderInZip.file(fileName, content);
                }
                console.log(`✓ Added ${fileName} to ${sceneName} zip`);
                filesAdded++;
            } else if (response.status !== 404) {
                console.log(`✗ Failed to fetch ${fileName}: ${response.status} from ${fileUrl}`);
            }
        } catch (e) {
            // File doesn't exist or fetch failed, skip silently
        }
    }
    console.log(`Total files added for ${sceneName}: ${filesAdded}`);
};

// Function to create and download scene folder as zip
const downloadSceneZip = async (sceneName: string, scenePath: string): Promise<void> => {
    try {
        const zip = new JSZip();

        // Create a folder for the scene
        const sceneFolder = zip.folder(sceneName);

        if (!sceneFolder) {
            throw new Error('Failed to create scene folder in zip');
        }

        // Extract directory path and filename
        const fullPath = `/assets/scenes/${scenePath.split('/').map(encodeURIComponent).join('/')}`;
        const pathParts = scenePath.split('/');
        const mjcfFileName = pathParts[pathParts.length - 1];
        const sceneDir = pathParts.slice(0, -1).join('/');
        const sceneDirPath = `/assets/scenes/${sceneDir.split('/').map(encodeURIComponent).join('/')}`;

        // Fetch the main MJCF file
        console.log(`Attempting to fetch main MJCF from: ${fullPath}`);
        try {
            const mjcfResponse = await fetch(fullPath, {
                mode: 'cors',
                headers: {
                    'Accept': '*/*'
                }
            });
            if (mjcfResponse.ok) {
                const mjcfContent = await mjcfResponse.text();
                sceneFolder.file(mjcfFileName, mjcfContent);
                console.log(`✓ Added main MJCF file: ${mjcfFileName}`);
            } else {
                console.log(`✗ Failed to fetch main MJCF file: ${mjcfResponse.status} ${mjcfResponse.statusText} from ${fullPath}`);
            }
        } catch (e) {
            console.log(`✗ Error fetching main MJCF file from ${fullPath}:`, e);
        }

        // Fetch all other files in the scene directory
        console.log(`Attempting to fetch scene contents from: ${sceneDirPath}`);
        await fetchSceneContents(sceneName, sceneDirPath, sceneFolder);

        // Generate the zip file
        const zipBlob = await zip.generateAsync({
            type: 'blob',
            compression: 'DEFLATE',
            compressionOptions: {
                level: 6
            }
        });

        // Download the zip
        const url = URL.createObjectURL(zipBlob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `${sceneName}_scene.zip`;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);

    } catch (error) {
        console.error('Failed to create scene zip:', error);
        throw error;
    }
};

const TaskGalleryVideo: React.FC<{selectedTask: string, taskData: any[]}> = ({selectedTask, taskData}) => {
    const selectedTaskData = taskData.find(t => t.name === selectedTask);
    const videoRefs = React.useRef<HTMLVideoElement[]>([]);
    const singleVideoRef = React.useRef<HTMLVideoElement>(null);
    const isUpdating = React.useRef(false);

    // Check if task has multiple videos
    if (selectedTaskData?.videos) {
        const handlePlay = (index: number) => {
            if (isUpdating.current) return;
            isUpdating.current = true;

            // Sync all videos when one is played
            videoRefs.current.forEach((video, i) => {
                if (video && i !== index) {
                    video.currentTime = videoRefs.current[index]?.currentTime || 0;
                    video.play().catch(() => {});
                }
            });

            setTimeout(() => {
                isUpdating.current = false;
            }, 100);
        };

        const handlePause = (index: number) => {
            if (isUpdating.current) return;
            isUpdating.current = true;

            // Pause all videos when one is paused
            videoRefs.current.forEach((video, i) => {
                if (video && i !== index) {
                    video.pause();
                }
            });

            setTimeout(() => {
                isUpdating.current = false;
            }, 100);
        };

        const handleSeek = (index: number) => {
            if (isUpdating.current) return;
            isUpdating.current = true;

            // Sync time when seeking
            const currentTime = videoRefs.current[index]?.currentTime || 0;
            videoRefs.current.forEach((video, i) => {
                if (video && i !== index) {
                    video.currentTime = currentTime;
                }
            });

            setTimeout(() => {
                isUpdating.current = false;
            }, 100);
        };

        return (
            <div className="space-y-4">
                <h3 className="text-lg font-semibold text-center text-gray-800">
                    {selectedTaskData.name === 'microwave-muffin' ? 'Microwave Muffin - 3DGS' : `${selectedTaskData.title} - Multiple Views`}
                </h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-2 max-w-4xl mx-auto">
                    {selectedTaskData.videos.map((video, index) => (
                        <div key={index} className="space-y-1">
                            <video
                                ref={(el) => {
                                    if (el) videoRefs.current[index] = el;
                                }}
                                src={video.src}
                                autoPlay
                                controls
                                muted
                                playsInline
                                loop
                                className="rounded-lg w-full"
                                style={{maxHeight: '280px', maxWidth: '400px', margin: '0 auto'}}
                                onPlay={() => handlePlay(index)}
                                onPause={() => handlePause(index)}
                                onSeeked={() => handleSeek(index)}
                            />
                        </div>
                    ))}
                </div>
            </div>
        );
    } else if (selectedTaskData?.placeholder) {
        // Placeholder display
        return (
            <div className="flex items-center justify-center bg-gray-100 rounded-lg w-full h-64 border-2 border-dashed border-gray-300">
                <div className="text-center">
                    <div className="text-gray-500 text-lg font-medium mb-2">
                        📹 Video Coming Soon
                    </div>
                    <div className="text-gray-700 text-sm">
                        {selectedTaskData.placeholder}
                    </div>
                </div>
            </div>
        );
    } else {
        // Single video display for other tasks
        React.useEffect(() => {
            if (singleVideoRef.current && selectedTaskData?.video) {
                singleVideoRef.current.src = selectedTaskData.video;
                singleVideoRef.current.load();
                singleVideoRef.current.play().catch(() => {
                    // Ignore autoplay errors
                });
            }
        }, [selectedTaskData?.video]);

        return (
            <video
                ref={singleVideoRef}
                autoPlay
                controls
                muted
                playsInline
                loop
                alt={`${selectedTaskData?.title} Task Rollouts`}
                className="rounded-lg w-full"
            />
        );
    }
};

